.header {
  margin-bottom: 30px;
  background-color: var(--secondary-color);
  padding: 15px 0;
  color: white;
}

.header__brand {
  color: white;
  text-decoration: none;
  font-size: 18px;
}

.header__brand:hover {
  color: rgba(255, 255, 255, 0.7);
}

.header__cart {
  position: relative;
}

.header__cart-inner {
  display: flex;
  align-items: flex-start;
  gap: 7px;
}

.header__cart-icon {
  height: 20px;
  width: 20px;
}

.header__cart-inner p {
  margin-bottom: 0;
  cursor: pointer;
}

.header__cart-inner p:hover {
  opacity: 0.7;
}

.header__cart-link {
  color: inherit;
  text-decoration: none;
}

.header__cart-link:hover {
  color: inherit;
}

.header__cart-content {
  position: absolute;
  right: 0;
  top: 100%;
  width: 500px;
  z-index: 999;
  border: 2px solid #f2f2f2;
  border-radius: 6px;
  background-color: #fff;
  padding: 15px 3px;
  box-shadow: 5px 5px 7px rgba(0, 0, 0, 0.5);
  display: none;
}

.cart__products {
  max-height: 350px;
  overflow-y: auto;
  padding: 0 9px 0 12px;
}

.cart__products::-webkit-scrollbar {
  width: 6px;
}

.cart__products::-webkit-scrollbar-track-piece {
  background-color: transparent;
  border-radius: 4px;
}

.cart__products::-webkit-scrollbar-thumb {
  background-color: #c1c1c1;
  border-radius: 4px;
}

.header__cart:hover .header__cart-content {
  display: block;
}

.cart__products {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.cart__products-item {
  color: black;
}

.cart__products-image {
  max-width: 100%;
  height: auto;
}

.cart__products-name {
  font-size: 14px;
  margin-bottom: 0;
  color: black;
  text-decoration: none;
  line-height: 10px;
}

.cart__products-name:hover {
  opacity: 0.8;
  cursor: pointer;
  color: black;
}

.cart__products-right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 10px;
}

.cart__products-price {
  font-size: 14px;
  margin-bottom: 0;
}

.cart__products-remove {
  cursor: pointer;
  font-size: 26px;

  &:hover {
    opacity: 0.7;
  }
}

.cart__driver-wrapper {
  padding: 0 15px;
}

.cart__driver {
  width: 100%;
  color: gray;
}

.cart__total {
  color: black;
  padding: 10px 15px;
  display: flex;
  justify-content: space-between;
}

.cart__total-text,
.cart__total-price {
  font-size: 18px;
  font-weight: 600;
}
