.product-list {
  .product__wrapper {
    background-color: #fff;
  }

  .product__wrapper:hover {
    transform: scale(1.03);
    transition: transform 0.2s ease;
  }

  .product__wrapper img {
    max-width: 100%;
    object-fit: cover;
  }

  .product__body {
    padding: 20px 30px;
    text-align: center;
  }

  .product__name {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 16px;
    margin-bottom: 10px;
    min-height: 48px;
  }

  .product__name:hover {
    opacity: 0.8;
    cursor: pointer;
  }

  .product__cost {
    color: gray;
    margin-bottom: 20px;
  }

  .produst__not-found {
    font-size: 20px;
  }
}

.product__cart {
  display: inline-block;
  color: white;
  padding: 0.44rem 20px;
  background-color: var(--primary-color);
  border-radius: 4px;
  cursor: pointer;
}

.product__cart.cart_added {
  background-color: var(--primary-h-color);
}

.product__cart:not(.cart_added):hover {
  background-color: var(--primary-h-color);
}
