@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap");
@import "vars.css";

body {
  font-family: "Montserrat", sans-serif;
  background-color: #f8f8f8 !important;
}

.text-default {
  --bs-text-opacity: 1;
  color: rgba(1, 173, 173, var(--bs-text-opacity)) !important;
}

.btn-default {
  color: #fff;
  background-color: var(--primary-color);
  border-color: var(--primary-color);
}

.btn-default:hover {
  color: #fff;
  background-color: var(--primary-h-color);
  border-color: var(--primary-h-color);
}

.loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

@import "./modules/header.scss";
@import "./modules/products.scss";
@import "./modules/cart.scss";
@import "./modules/single-product.scss";
@import "./modules/sidebar.scss";
@import "./modules/pagination.scss";
@import "./modules/demo.scss";
