.sidebar-wrapper {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.sidebar-block {
  padding: 15px 20px;
  border: 2px solid #f2f2f2;
  background-color: #fff;
}

.text-widget__contacts {
  display: flex;
  flex-direction: column;
  gap: 15px;

  p {
    margin-bottom: 0;
    display: flex;
    align-items: center;
    gap: 5px;

    .text-widget__img {
      flex: 0 0 22px;

      img {
        height: 18px;
        width: 18px;
      }
    }

    a {
      text-decoration: none;
      color: grey;
    }

    a:hover {
      opacity: 0.7;
    }
  }
}

// Nav Widget
.nav-widget {
  .nav-widget__list {
    display: flex;
    flex-direction: column;
    gap: 1px;
    padding: 0;
    margin: 0;
    list-style: none;
  }

  .nav-widget__item {
    a {
      display: block;
      padding: 7px 10px;
      text-decoration: none;
      color: gray;
    }
  }

  .nav-widget__item:hover {
    transition: background-color 0.3s ease, color 0.3s ease;
    background-color: #d3d3d3;

    a {
      color: black;
    }
  }
}
