.cart__title {
  margin-bottom: 15px;
}

/* Card Content */
.cart__content {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 5px;
  display: block;
  max-height: calc(100vh - 90px);
  overflow-y: auto;
}

.cart__content tr td {
  border-left: 1px solid #d6d7d6;
  border-top: 1px solid #d6d7d6;
  border-bottom: 1px solid #d6d7d6;
  padding-left: 15px;
  padding-right: 15px;
  text-align: center;
}

.cart__content-head,
.cart__content-foot {
  position: sticky;
  z-index: 99;
}

.cart__content-body {
  position: relative;
  z-index: 1;
}

.cart__content-head {
  top: 0;
}

.cart__content-foot {
  bottom: 0;
}

.cart__content-foot tr td {
  background-color: #fff;
}

.cart__content tr:not(.cart__content-result) td:nth-child(-n + 2) {
  text-align: left;
}

.cart__content tr td:last-child {
  border-right: 1px solid #d6d7d6;
}

/* THead */
.cart__content-head tr td {
  font-weight: 600;
  background-color: #f2f2f2;
  padding-top: 15px;
  padding-bottom: 15px;
}

/* TBody */
.cart__content-body tr:not(.cart__content-result) td:first-child {
  padding: 7px;
}

.cart__content-body tr td {
  background-color: #fff;
}

.cart__content-img img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.cart__content-name {
  font-weight: 600;
}

.cart__content-name a {
  color: black;
  text-decoration: none;
}

.cart__content-name a:hover {
  color: black;
  opacity: .7;
}

.cart__content-total {
  font-weight: 600;
}

.cart__content-remove img {
  height: 22px;
  width: 22px;
  cursor: pointer;
}

/* Result */
.cart__content-result td {
  font-weight: 600;
  padding: 15px;
  text-align: center;
}

.cart__content-result td:first-child {
  text-transform: uppercase;
  text-align: left;
}

// Checkout
.checkout-title {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  position: relative;
  margin-bottom: 20px;
  font-size: 24px;

  &:after {
    position: absolute;
    left: 0;
    content: "";
    width: 100%;
    height: 1px;
    color: black;
  }
}

.checkout-form {
  .form-label {
    font-weight: 600;
  }

  .form-check {
    margin-bottom: 12px;
  }
}