.pagination_list {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-top: 20px;
}

.pagination_item {
  padding: 5px 0;
  width: 34px;
  text-align: center;
  background-color: var(--primary-color);
  font-size: 1rem;
  color: white;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease-out;

  &.current_page {
    background-color: var(--secondary-color);
  }

  &:not(.current_page):hover {
    background-color: var(--primary-h-color);
  }
}
