.single-product {
  .product-wrapper {
    border: var(--border);
    padding: 30px;
    background-color: #fff;
    margin-bottom: 20px;
  }

  .product-image img {
    max-width: 100%;
    height: auto;
  }

  .product-body {
    text-align: center;
  }

  .product-name {
    font-size: 22px;
    margin-bottom: 10px;
  }

  .product-price {
    color: gray;
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 25px;
  }

  .product__add-cart {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-bottom: 20px;
  }

  .product-qty {
    width: 4em;
    text-align: center;
  }

  .product-categories {
    color: grey;
  }

  .product-description p:last-child {
    margin-bottom: 0;
  }

  .product-images__preview {
    display: flex;
    align-items: center;

    img {
      $img_sizes: 76px;
      height: $img_sizes;
      width: $img_sizes;
      object-fit: cover;
      cursor: pointer;
    }
  }

  .product-images__preview {
    .owl-item img {
      transition: opacity 0.3s ease-out;
    }

    .owl-item:not(.current) img {
      opacity: 0.7;
    }
  }

  .previewSlider {
    position: relative;
    $offset: -20px;

    .owl-prev,
    .owl-next {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      height: 100%;

      span {
        display: flex;
        align-items: center;
        font-size: 36px;
        height: 100%;
      }

      &.disabled {
        display: none;
      }
    }

    .owl-prev {
      left: $offset;
    }

    .owl-next {
      right: $offset;
    }
  }
}
