.main__filter {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.main__filter-block {
  padding: 20px 25px;
  border: 2px solid #f2f2f2;
  background-color: #fff;
  border-radius: 4px;
}

.filter-item__title {
  margin-bottom: 15px;
  font-size: 17px;
}

.filter-item__content {
  display: flex;
  align-items: center;
  position: relative;

  .clear-search {
    display: flex;
    align-items: center;
    position: absolute;
    right: 10px;
    font-size: 24px;
    font-weight: 600;
    cursor: pointer;

    &:hover {
      opacity: 0.7;
    }
  }
}

.filter-item__child {
  display: flex;
  align-items: center;
  gap: 10px;
}

.filter-item__child p {
  margin-bottom: 0;
  font-size: 18px;
}

.filter-item__by-cost {
  text-align: center;
  font-weight: 600;
  font-size: 16px;
  margin-top: 10px;
  margin-bottom: 0;
}
