oducts-price{font-size:14px;margin-bottom:0}.cart__products-remove{cursor:pointer;font-size:26px}.cart__products-remove:hover{opacity:0.7}.cart__driver-wrapper{padding:0 15px}.cart__driver{width:100%;color:gray}.cart__total{color:black;padding:10px 15px;display:flex;justify-content:space-between}.cart__total-text,.cart__total-price{font-size:18px;font-weight:600}.product-list .product__wrapper{background-color:#fff}.product-list .product__wrapper:hover{-webkit-transform:scale(1.03);transform:scale(1.03);transition:-webkit-transform 0.2s ease;transition:transform 0.2s ease;transition:transform 0.2s ease, -webkit-transform 0.2s ease}.product-list .product__wrapper img{max-width:100%;object-fit:cover}.product-list .product__body{padding:20px 30px;text-align:center}.product-list .product__name{display:flex;align-items:center;justify-content:center;font-weight:bold;font-size:16px;margin-bottom:10px;min-height:48px}.product-list .product__name:hover{opacity:0.8;cursor:pointer}.product-list .product__cost{color:gray;margin-bottom:20px}.product-list .produst__not-found{font-size:20px}.product__cart{display:inline-block;color:white;padding:0.44rem 20px;background-color:var(--primary-color);border-radius:4px;cursor:pointer}.product__cart.cart_added{background-color:var(--primary-h-color)}.product__cart:not(.cart_added):hover{background-color:var(--primary-h-color)}.cart__title{margin-bottom:15px}.cart__content{width:100%;border-collapse:separate;border-spacing:0 5px;display:block;max-height:calc(100vh - 90px);overflow-y:auto}.cart__content tr td{border-left:1px solid #d6d7d6;border-top:1px solid #d6d7d6;border-bottom:1px solid #d6d7d6;padding-left:15px;padding-right:15px;text-align:center}.cart__content-head,.cart__content-foot{position:-webkit-sticky;position:sticky;z-index:99}.cart__content-body{position:relative;z-index:1}.cart__content-head{top:0}.cart__content-foot{bottom:0}.cart__content-foot tr td{background-color:#fff}.cart__content tr:not(.cart__content-result) td:nt