lor)}.pagination_item:not(.current_page):hover{background-color:var(--primary-h-color)}.wrapperAlert{position:fixed;bottom:0;width:100%;padding:15px 10px;background-color:rgba(1,173,173,0.7);display:flex;justify-content:center;align-items:center;color:white;z-index:999}.wrapperAlert_text{display:block;font-size:1.15rem;text-align:center;padding-right:40px}.wrapperAlert_close{position:absolute;right:20px;font-size:30px;cursor:pointer}.wrapperAlert_close:hover{opacity:0.7}

